<template>
	<view class="container">
		<view class="page-body">
			<view class="header">
				<u-gap height="10"></u-gap>
				<view class="title">
					<view class="input">
						<input type="text" class="title-input" focus placeholder="标题"
							placeholder-style="font-size:20px"></input>
					</view>
					<view class="picker" @click="selectPicture()">
						<view class="upload-view">
							<uni-icons type="plusempty" size="20"></uni-icons>
							<view class="text"> 上传封面图 </view>
						</view>
					</view>
				</view>
				<u-gap height="10"></u-gap>
				<view style="">
					<u-line length="92%" margin="auto" direction="row"></u-line>
				</view>
			</view>
			<view class="editor-wrapper">
				<editor id="editor" class="ql-container" placeholder="内容..." show-img-size show-img-toolbar
					show-img-resize @statuschange="onStatusChange" :read-only="readOnly" @ready="onEditorReady">
				</editor>
			</view>
			<view class='wrapper'>
				<view class='toolbar' @tap="format" style="height: 80px;overflow-y: auto;">
					<view :class="formats.bold ? 'ql-active' : ''" class="iconfont icon-zitijiacu" data-name="bold">
					</view>
					<view :class="formats.italic ? 'ql-active' : ''" class="iconfont icon-zitixieti" data-name="italic">
					</view>
					<view :class="formats.underline ? 'ql-active' : ''" class="iconfont icon-zitixiahuaxian"
						data-name="underline"></view>
					<view :class="formats.strike ? 'ql-active' : ''" class="iconfont icon-zitishanchuxian"
						data-name="strike"></view>
					<!-- #ifndef MP-BAIDU -->
					<view :class="formats.align === 'left' ? 'ql-active' : ''" class="iconfont icon-zuoduiqi"
						data-name="align" data-value="left"></view>
					<!-- #endif -->
					<view :class="formats.align === 'center' ? 'ql-active' : ''" class="iconfont icon-juzhongduiqi"
						data-name="align" data-value="center"></view>
					<view :class="formats.align === 'right' ? 'ql-active' : ''" class="iconfont icon-youduiqi"
						data-name="align" data-value="right"></view>
					<view :class="formats.align === 'justify' ? 'ql-active' : ''" class="iconfont icon-zuoyouduiqi"
						data-name="align" data-value="justify"></view>
					<!-- #ifndef MP-BAIDU -->
					<view :class="formats.lineHeight ? 'ql-active' : ''" class="iconfont icon-line-height"
						data-name="lineHeight" data-value="2"></view>
					<view :class="formats.letterSpacing ? 'ql-active' : ''" class="iconfont icon-Character-Spacing"
						data-name="letterSpacing" data-value="2em"></view>
					<view :class="formats.marginTop ? 'ql-active' : ''" class="iconfont icon-722bianjiqi_duanqianju"
						data-name="marginTop" data-value="20px"></view>
					<view :class="formats.marginBottom ? 'ql-active' : ''" class="iconfont icon-723bianjiqi_duanhouju"
						data-name="marginBottom" data-value="20px"></view>
					<!-- #endif -->

					<view class="iconfont icon-clearedformat" @tap="removeFormat"></view>

					<!-- #ifndef MP-BAIDU -->
					<view :class="formats.fontFamily ? 'ql-active' : ''" class="iconfont icon-font"
						data-name="fontFamily" data-value="Pacifico"></view>
					<view :class="formats.fontSize === '24px' ? 'ql-active' : ''" class="iconfont icon-fontsize"
						data-name="fontSize" data-value="24px"></view>
					<!-- #endif -->
					<view :class="formats.color === '#0000ff' ? 'ql-active' : ''" class="iconfont icon-text_color"
						data-name="color" data-value="#0000ff"></view>
					<view :class="formats.backgroundColor === '#00ff00' ? 'ql-active' : ''"
						class="iconfont icon-fontbgcolor" data-name="backgroundColor" data-value="#00ff00"></view>
					<view class="iconfont icon-date" @tap="insertDate"></view>
					<view class="iconfont icon--checklist" data-name="list" data-value="check"></view>
					<view :class="formats.list === 'ordered' ? 'ql-active' : ''" class="iconfont icon-youxupailie"
						data-name="list" data-value="ordered"></view>
					<view :class="formats.list === 'bullet' ? 'ql-active' : ''" class="iconfont icon-wuxupailie"
						data-name="list" data-value="bullet"></view>

					<view class="iconfont icon-undo" @tap="undo"></view>
					<view class="iconfont icon-redo" @tap="redo"></view>

					<view class="iconfont icon-outdent" data-name="indent" data-value="-1"></view>
					<view class="iconfont icon-indent" data-name="indent" data-value="+1"></view>
					<view class="iconfont icon-fengexian" @tap="insertDivider"></view>
					<view class="iconfont icon-charutupian" @tap="insertImage"></view>
					<view :class="formats.header === 1 ? 'ql-active' : ''" class="iconfont icon-format-header-1"
						data-name="header" :data-value="1"></view>
					<view :class="formats.script === 'sub' ? 'ql-active' : ''" class="iconfont icon-zitixiabiao"
						data-name="script" data-value="sub"></view>
					<view :class="formats.script === 'super' ? 'ql-active' : ''" class="iconfont icon-zitishangbiao"
						data-name="script" data-value="super"></view>

					<view class="iconfont icon-shanchu" @tap="clear"></view>

					<view :class="formats.direction === 'rtl' ? 'ql-active' : ''" class="iconfont icon-direction-rtl"
						data-name="direction" data-value="rtl"></view>
				</view>
			</view>
		</view>
	</view>
</template>


<script>
	import btCropper from '@/uni_modules/qf-image-cropper/components/qf-image-cropper/qf-image-cropper.vue';
	export default {

		data() {
			return {
				readOnly: false,
				formats: {},
				// 封面路径
				coverImage: '',
				imgPath: '/static/uploads/images',
			}
		},
		onLoad() {

		},
		methods: {
			// 跳转到裁剪页面
			async toCrop(pendingImage) {
				await uni.navigateTo({
					url: `/pages/crop/crop?scr=${pendingImage}`,
				})
				const eventChannel = this.getOpenerEventChannel();
				eventChannel.on('returnImage', (data) => {
					// data.imagePath 就是你需要的图片路径
					console.log('Received image path:', data.imagePath);
					// 在这里更新你的数据模型
					this.imagePath = data.imagePath;
				});
			},
			// 实现选择并上传图片的功能
			async uploadImage() {
				const path = '/files/images';
				let imgData = await uni.chooseImage({
					count: 1,
				})
				console.log(imgData);
				const tempFilePath = imgData.tempFilePaths[0];
				console.log(tempFilePath);
				let uploadData = await uni.uploadFile({
					url: getApp().globalData.URL + path,
					filePath: tempFilePath,
					name: 'img',
					header: {
						'token': uni.getStorageSync('token'),
					},
				})
				console.log(uploadData);
				const imgName = JSON.parse(uploadData.data).data;
				return imgName;
			},
			// 点击上传封面图时触发
			async selectPicture() {
				this.toCrop();
				// const imgName = this.uploadImage();
				// data返回图片名
				// this.coverImage = getApp().globalData.imgPath + imgName;
			},
			// 获取富文本html内容
			getEditorContent() {
				let html;
				this.editorCtx.getContents({
					success: function(res) {
						html = res.html;
						console.log(html);
						console.log(res.delta);
					}
				})
				return html;
			},
			/* -------------富文本编辑器功能实现---------------------- */
			onEditorReady() {
				// #ifdef MP-BAIDU
				this.editorCtx = requireDynamicLib('editorLib').createEditorContext('editor');
				// #endif

				// #ifdef APP-PLUS || MP-WEIXIN || H5
				uni.createSelectorQuery().select('#editor').context((res) => {
					this.editorCtx = res.context
				}).exec()
				// #endif
			},
			undo() {
				this.editorCtx.undo()
			},
			redo() {
				this.editorCtx.redo()
			},
			format(e) {
				let {
					name,
					value
				} = e.target.dataset
				if (!name) return
				// console.log('format', name, value)
				this.editorCtx.format(name, value)
			},
			onStatusChange(e) {
				const formats = e.detail
				this.formats = formats
			},
			insertDivider() {
				// -----------------------------------------------------------
				this.getEditorContent();
				// ---------------------------------------------
				this.editorCtx.insertDivider({
					success: function() {
						console.log('insert divider success')
					}
				})
			},
			clear() {
				uni.showModal({
					title: '清空编辑器',
					content: '确定清空编辑器全部内容？',
					success: res => {
						if (res.confirm) {
							this.editorCtx.clear({
								success: function(res) {
									console.log("clear success")
								}
							})
						}
					}
				})
			},
			removeFormat() {
				this.editorCtx.removeFormat()
			},
			insertDate() {
				const date = new Date()
				const formatDate = `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`
				this.editorCtx.insertText({
					text: formatDate
				})
			},
			async insertImage() {
				const imgName = await this.uploadImage();
				this.editorCtx.insertImage({
					src: getApp().globalData.imgPath + imgName,
					alt: '图像',
					success: function() {
						console.log('insert image success')
					}
				})
			}
		},
	}
</script>


<style lang="less" scoped>
	@import "./editor-icon.css";

	.container {
		height: calc(100vh - var(--window-top));
	}

	.header {
		height: 70px;
	}

	.title {
		display: flex;
		width: 92%;
		// height: 100px;
		margin: auto;
		align-items: center;

	}

	.input {
		flex-grow: 1;
		font-size: 20px;
		font-weight: bold;
		height: 100%;
	}

	.page-body {
		height: calc(100vh - var(--window-top) - var(--status-bar-height));
	}

	.picker {
		// background-color: red;
		min-width: 90px;
		max-width: 90px;
		height: 50px;
		margin-left: 10px;
		border: #EEE 2px solid;
	}


	.upload-view {
		background-color: WhiteSmoke;
		// width: 70px;
		min-width: 90px;
		min-height: 50px;
		height: 50px;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;


		.text {
			font-size: 12px;
		}

	}

	/* 	.wrapper {
		height: 100%;
	} */

	.editor-wrapper {
		/* 80px为wrapper 工具的高度，50px为height的高度 */
		height: calc(100vh - var(--window-top) - var(--status-bar-height) - 80px - 70px);
		background: #fff;
	}

	.iconfont {
		display: inline-block;
		padding: 8px 8px;
		width: 24px;
		height: 24px;
		cursor: pointer;
		font-size: 20px;
	}

	.toolbar {
		box-sizing: border-box;
		border-bottom: 0;
		font-family: 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
	}

	.ql-container {
		box-sizing: border-box;
		padding: 12px 15px;
		width: 100%;
		min-height: 30vh;
		height: 100%;
		/* margin-top: 20px; */
		font-size: 16px;
		line-height: 1.5;
	}

	.ql-active {
		color: #06c;
	}
</style>